import React, { useState, useEffect, useMemo } from "react";
import MaterialTable from "material-table";
import { makeStyles } from "@material-ui/core/styles";
import { Box, Button, HStack, Text } from "@chakra-ui/react";
import { GrTrash } from "react-icons/gr";
import { useRecoilState } from "recoil";
//import AddIcon from "@material-ui/icons/Add";
//import EditIcon from "@material-ui/icons/Edit";
//import DeleteIcon from "@material-ui/icons/Delete";
//import CheckIcon from "@material-ui/icons/Check";
//import SearchIcon from "@material-ui/icons/Search";
//import LeaveForm from "./LeaveForm";
//import { CustomDialog } from "../helpers/CustomDialog";
//import { AlertDialog } from "../helpers/AlertDialogBox";
import { useLeavesPeriodbyMth } from "./leaves/useLeavesPeriodbyMth";
import { useUpdateLeaves } from "./leaves/useUpdateLeaves";
import { AlertDialogBox } from "../helpers/AlertDialogBox";
import { loginLevelState } from "./data/atomdata";
//const FILTERSTRING = "Pending";

const initial_form = {
  name: "",
  to_date: "",
  from_date: "",
  reason: "",
  status: "Pending",
  no_of_days: 0,
  leave_bal: 0,
  reporting_email: "",
};

export default function LeaveTableViewSummary({ year, month }) {
  const classes = useStyles();
  const [loginLevel, setLoginLevel] = useRecoilState(loginLevelState);
  const { leavesperiodbymth, setLeavePeriodMthId, setLeavePeriodYrId } =
    useLeavesPeriodbyMth();
  const updateLeave = useUpdateLeaves();
  const [formdata, setFormdata] = useState(initial_form);
  const [isAlertOpen, setIsAlertOpen] = useState(false);
  const [editLeaveID, setEditLeaveID] = useState("")

  const columns = useMemo(
    () => [
      {
        title: "Name",
        field: "name",
      },

      {
        title: "From Date",
        field: "from_date",
        type: "date",
        dateSetting: { locale: "en-GB" },
        editable: "never",
      },
      {
        title: "To Date",
        field: "to_date",
        type: "date",
        dateSetting: { locale: "en-GB" },
        editable: "never",
      },
      {
        title: "No of Days",
        field: "no_of_days",
        type: "numeric",
        editable: "never",
      },
      {
        title: "Reason",
        field: "reason",
        editable: "never",
      },
      { title: "Status", field: "status" },
    ],
    []
  );

  useEffect(() => {
    setLeavePeriodYrId(year);
    setLeavePeriodMthId(month);
  }, []);


  const Delete_Leave = (data) => {
    //console.log("del data", data);
    const { id, attachmentid, rec_id, tableData, ...fields } = data;
    const editData = { ...fields, posted: "D" };

    setFormdata({ ...editData });
    setFormdata({ ...editData });
    setEditLeaveID(id);
    handleAlertOpen();
  };

  const handleAlertOpen = () => {
    setIsAlertOpen(true);
  };
  const handleAlertClose = () => {
    setIsAlertOpen(false);
  };

  const handleOnDeleteConfirm = () => {
    const upddata = { ...formdata, posted: "D" };
    updateLeave({ id: editLeaveID, ...upddata });
    // const id = editExpenseID;
    // deleteExpenses(id);
    // expensesattachments
    //   .filter((r) => r.attachmentid === expattachId)
    //   .forEach((rec) => {
    //     const id = rec.id;
    //     deleteExpensesAttachment(id);
    //   });
  };

  return (
    <div className={classes.root}>
      {/* <h1>Expenses Claims Application</h1> */}

      {/* <div style={{ maxWidth: "100%", paddingTop: "5px" }}> */}

      <Box maxW="100%" pt="5px" overflow="scroll">
        <MaterialTable
          columns={columns}
          data={leavesperiodbymth}
          title="Leave Application"
          actions={[
            (rowData) => ({
              icon: () => <GrTrash size="23px" />,
              hidden: loginLevel.loginLevel !== "Manager",
              tooltip: "Delete",
              onClick: (event, rowData) => {
                Delete_Leave(rowData);
              },
            }),
          ]}
          options={{
            filtering: false,
            search: false,
            toolbar: false,
            paging: false,
            headerStyle: {
              backgroundColor: "rgba(75, 192, 192, 1)",
              color: "white",
            },
            showTitle: false,
          }}
        />
      </Box>
      <AlertDialogBox
        onClose={handleAlertClose}
        onConfirm={handleOnDeleteConfirm}
        isOpen={isAlertOpen}
        title="Delete Leave"
      >
        <h2>
          Are you sure you want to delete {formdata.name} leave from {formdata.from_date} to {formdata.to_date} ?
        </h2>
      </AlertDialogBox>
    </div>
    // </div>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    padding: 0,
  },
}));
